html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

:root {
  --primary-color: #00acdc;
}

body {
  background: #e5e5e5;
  margin: 0;
}

.header {
  background: #fff;
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo__link {
  width: 40px;
  height: 40px;
  display: inline-block;
}

.nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  list-style: none;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: flex;
}

.nav__link:hover {
  color: var(--primary-color);
}

.is-active {
  color: var(--primary-color);
  position: relative;
}

.is-active:after {
  background-color: var(--primary-color);
  content: "";
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}
